import React, {useState} from 'react';
import './App.css';

const api = {
  key: "99e274e8d9087c5cf7aa26e78d3abf77" ,
  base: "https://api.openweathermap.org/data/2.5/"
}

function App() {
  const [query, setQuery]= useState('');
  const [weather, setWeather]= useState({});
  
  const search = evt => {
    if (evt.key === "Enter") {
      fetch(`${api.base}weather?q=${query}&units=metric&appid=${api.key}`)
      .then(res => res.json())
      .then(result => { setQuery(''); setWeather(result);
      console.log(result)
    });
  }
}

  const dateBuilder = (d) => {
    /** ~ English
    let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
    let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
    **/

    //Akan
    let months = ["Ɔpɛpɔn", "Ɔgyefoɔ", "Ɔbɛnem", "Oforisuo", "Kotonimma", "Ayɛwohomumɔ", "Kitawonsa", "Ɔsanaa", "Ɛbɔ", "Ahinime", "Obubuo", "Ɔpɛnimma"];
    let days = ["Kwasiada", "Jowada", "Benada", "Wukuada", "Yawóada", "Fiada", "Memeneda"];

    //Hausa 
    /**
    let months = ["Janairu", "Faburairu", "Maris", "Afirilu", "Mayu", "Yuni", "Yuli", "Agusta", "Satumba", "Oktoba", "Nuwamba", "Disamba"];
    let days = ["lahadi", "litinin", "Talata", "laraba", "Alhamis", "Jumma", "Asabar"];
    **/

    let day = days[d.getDay()];
    let date = d.getDate();
    let sup = "";
    let month = months[d.getMonth()];
    let year = d.getFullYear();
    
    if (date == 1 || date == 21 || date ==31)
      {
        sup = "st";
      }
        else if (date == 2 || date == 22)
      {
        sup = "nd";
      }
        else if (date == 3 || date == 23)
      {
        sup = "rd";
      }
        else
      {
        sup = "th";
      }


    return `${day} ${date} ${sup} ${month} ${year}`
  }

  var facts = [
    '“Only a fool tests the depth of a river with no feet.”', 
    '“Examine what is said, not who is speaking.”', 
    '“If you want to know the end, look at the beginning.”',
    '“Knowledge is a garden. If it isn’t cultivated, you can’t harvest it.”',
    '“Do not look where you feel. Look where you slipped.”', 
    '“However long the night, the dawn will break.”', 
    '“Rain does not fall on one roof alone.”',
    '“However far a stream flows, it doesn’t forget its origin.”'
  ];
  var randomFact = Math.floor(Math.random() * facts.length);



  return (
    <div className={(typeof weather.main != "undefined") ? ((weather.main.temp > 22) ? 'App warm' : 'App'): 'app'}>
      <main>
{/*
      <form>
            <select name="backdrop" id="background" class="form-select" onchange="raGaraGaMe(this)">
                    <option value="english">English</option>
                    <option value="hausa">Hausa</option>
                    <option value="akan">Akan</option>
                    <option value="ewe">Ewe</option>
            </select>
      </form>
*/}
        <div className="search-box">
          <input type="text" className="search-bar" placeholder="Search..." onChange={e => setQuery(e.target.value)} value={query} onKeyPress={search}/>
        </div>
        {(typeof weather.main != "undefined") ? (
        <div>
          <div className="location-box">
            <div className="location">{weather.name}, {weather.sys.country}</div>
            <div className="date">{dateBuilder(new Date())}</div>
          </div>
          <div className="weather-box">
            <div className="temperature">
              {Math.round(weather.main.temp)}˚c
            </div>
            <div className="weather">{weather.weather[0].description}</div>
          </div>
          <div className="weather-box">
              <div className="quotes">{facts[randomFact]}</div> 
          </div>           
          </div>  
        ) : ('')}
      </main>
    </div>
  );
}

export default App;
